import React from "react";
import styled from "styled-components";
import "../../style/diceIntro.css";

import dice1 from "../../img/dice/dice1.png";
import dice2 from "../../img/dice/dice2.svg";
import dice3 from "../../img/dice/dice3.png";
import dice4 from "../../img/dice/dice4.svg";
import dice5 from "../../img/dice/dice5.svg";

const IntroDice = () => {
  const DiceFloor = styled.div`
    width: 300px;
    height: 300px;
    padding: 1rem;
    border: 6px solid var(--point-color);
    position: absolute;
    background-color: #fff;
    @media screen and (max-width: 767px) {
      width: 200px;
      height: 200px;
    }
  `;
  return (
    <article className="stage">
      <div id="cube">
        <DiceFloor id="diceFront" className="diceFloor clay">
          <img src={dice1} alt="Dice_Img" />
        </DiceFloor>
        <DiceFloor id="diceBack" className="diceFloor clay">
          <img src={dice3} alt="Dice_Img" />
        </DiceFloor>
        <DiceFloor id="diceUp" className="diceFloor clay">
          <img src={dice2} alt="Dice_Img" />
        </DiceFloor>
        <DiceFloor id="diceDwn" className="diceFloor clay">
          <img src={dice3} alt="Dice_Img" />
        </DiceFloor>
        <DiceFloor id="diceLeft" className="diceFloor clay">
          <img src={dice4} alt="Dice_Img" />
        </DiceFloor>
        <DiceFloor id="diceRight" className="diceFloor clay">
          <img src={dice5} alt="Dice_Img" />
        </DiceFloor>
      </div>
    </article>
  );
};

export default IntroDice;
