/* Functions */
import React from "react";

/* Library */
import { styled } from "styled-components";
import Slider from "react-slick";
import "./styles.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Monitor = ({ folioState }) => {
  /* Styled-Component */
  const MonitorArt = styled.article`
    width: 100%;
    padding: 1rem;
    & > .SliderCover {
      height: 480px;
    }
    & > .SliderCover > div {
      height: 480px;
      & > .slick-list {
        height: 480px;
      }
    }
    & > .monitorTitle {
      color: white;
      width: 80%;
      padding: 1rem;
      border-radius: 4rem;
      text-align: center;
      font-size: 1.3rem;
      margin: 0 auto 0.5rem;
      & > h2 {
        color: var(--point-color);
      }
    }
    & > .etc {
      & > ul {
        padding-left: 2rem;
        & > li {
          list-style: disc;
          margin-bottom: 1rem;
          font-weight: 600;
        }
      }
    }
    & > .stack {
      & > ul {
        width: 40%;
        margin: auto;
        display: flex;
        justify-content: space-between;
      }
    }
    & > div {
      & > .monitorSub {
        width: 40%;
        padding: 1rem;
        border-radius: 1.5rem;
        margin: 1rem auto 3rem;
        text-align: center;
        font-size: 2rem;
        font-weight: 600;
        color: var(--point-color);
      }

      & > ul {
        width: fit-content;
        margin: auto;
        & > li {
          margin-bottom: 1rem;
          font-weight: 600;
        }
      }
    }
    & > .gotopage {
      width: 350px;
      margin: auto;
      & > div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        & > button {
          padding: 1rem;
          border: none;
          border-radius: 1.5rem;
          margin: 1rem 1rem 2rem;
          color: #020715;
          background-color: white;
          box-shadow: inset 0px 0px 7px #020715;
          transition: 550ms;
          font-weight: 700;
          font-size: 1.5rem;
          cursor: pointer;
          transition: 550ms;
          &:hover {
            color: var(--point-color);
            background-color: #020715;
            background-color: var(--point-bg);
            box-shadow: inset 5px 5px 7px rgba(112, 219, 255, 0.55),
              inset -5px -5px 7px rgba(56, 125, 148, 0.55);
          }
        }
      }
    }
    @media screen and (max-width: 767px) {
      padding: 0;
      & > .monitorTitle {
        color: white;
        width: 90%;
        padding: 1rem;
        border-radius: 4rem;
        text-align: center;
        font-size: 1rem;
        margin: 0 auto 2rem;
        & > h2 {
          color: var(--point-color);
          font-weight: normal;
        }
      }
      & > .SliderCover {
        width: calc(100% - 3.2rem);
        height: 200px;
        margin: auto;
      }
      & > .SliderCover > div {
        height: 200px;
        & > .slick-list {
          height: 200px;
        }
      }
      & > .gotopage {
        width: fit-content;
        margin: auto;
        & > div {
          & > button {
            margin: 1rem;
            font-size: 1.3rem;
          }
        }
      }
      & > .stack {
        & > ul {
          width: 80%;
          display: flex;
          justify-content: space-between;
        }
      }
      & > div {
        & > .clay {
          width: 40%;
          padding: 1rem;
          border-radius: 1.5rem;
          margin: 1rem auto;
          text-align: center;
          font-size: 1.3rem;
          font-weight: 500;
        }
        & > ul {
          width: calc(100% - 1rem);
          margin: auto;
          & > img {
            width: 15%;
            height: 15%;
            display: block;
            margin: auto;
            object-fit: contain;
            object-position: center;
          }
        }
      }
    }
  `;

  const sliderSetup = {
    slidesPerView: 1,
    spaceBetween: 30,
    infinite: false,
    mousewheel: true,
    navigation: true,
  };
  return (
    <MonitorArt>
      <h1 className="monitorTitle">
        <h2>{folioState.title}</h2> {folioState.date}
      </h1>
      {/* Swiper */}
      <article className="SliderCover" {...sliderSetup}>
        <Slider className="portPics">
          <div>
            <img src={folioState.pc} alt="PC Slider IMG" />
          </div>
          <div>
            <img src={folioState.mobile} alt="" />
          </div>
        </Slider>
      </article>
      {/* Informations */}
      <div className="gotopage">
        <div>
          <button
            className="pageBtn"
            onClick={() => {
              window.open(folioState.demo);
            }}
          >
            Demo
          </button>
          <button
            className="pageBtn"
            onClick={() => {
              window.open(folioState.github);
            }}
          >
            GitHub
          </button>
        </div>
      </div>
      <div className="stack">
        <p className="monitorSub">Stacks</p>
        <ul>
          {folioState.stack.map((things) => (
            <>
              <img key={things} src={things} alt="Skill Stack Icon Img" />
            </>
          ))}
        </ul>
      </div>
      <div className="info">
        <p className="monitorSub">Inform</p>
        <ul>
          <li>참여 인원: {folioState.people} 명</li>
          <li>
            파트:{" "}
            {folioState.part.map((things, idx) =>
              idx === folioState.part.length - 1 ? (
                <span key={things}>{things}</span>
              ) : (
                <span key={things}>{things}, </span>
              )
            )}
          </li>
        </ul>
      </div>
      <div className="etc">
        <p className="monitorSub">Additional</p>
        <ul>
          {folioState.etc.map((things) => (
            <li key={things}>{things}</li>
          ))}
        </ul>
      </div>
    </MonitorArt>
  );
};

export default Monitor;
