import React, { useState } from "react";
import Monitor from "./Monitor";

/* List State Files */
import PortFolioList from "./Port";
import PortList from "../../json/portfolios.json";

/* Library */
import styled from "styled-components";

const Portfolios = () => {
  /* PortFolio State */
  const [changePort, setChangePort] = useState([true, "", "", ""]);
  const [folioState, setFolioState] = useState(PortFolioList[0]);

  /* 버튼 클릭 시 Event */
  const changePorts = (idx) => {
    if (idx === 0) {
      setChangePort([true, "", "", ""]);
    } else if (idx === 1) {
      setChangePort(["", true, "", ""]);
    } else if (idx === 2) {
      setChangePort(["", "", true, ""]);
    } else {
      setChangePort(["", "", "", true]);
    }
    setFolioState(PortFolioList[idx]);
  };
  const Port = styled.section`
    width: 100%;
    & > .PortBtnsWrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 900px;
      margin: 1rem auto 0;
    }
    & > .porttitle {
      width: 700px;
      padding: 1.5rem;
      border-radius: 60px;
      margin: 5rem auto 1rem;
      text-align: center;
      font-size: 2.5rem;
      font-weight: 700;
    }
    @media screen and (max-width: 767px) {
      & > .PortBtnsWrap {
        width: 100%;
        padding: 1rem;
        gap: 1rem;
      }
      & > .porttitle {
        width: calc(100% - 1rem);
        margin: auto;
      }
    }
  `;
  const PortBtn = styled.button`
    padding: 1rem;
    border: none;
    border-radius: 1.5rem;
    margin: 2rem 1rem 0;
    color: #020715;
    background-color: white;
    box-shadow: inset 0px 0px 7px #020715;
    transition: 550ms;
    font-weight: 700;
    font-size: 1.5rem;
    cursor: pointer;
    &.active {
      color: var(--point-color);
      background-color: #020715;
      background-color: var(--point-bg);
      box-shadow: inset 5px 5px 7px rgba(112, 219, 255, 0.55),
        inset -5px -5px 7px rgba(56, 125, 148, 0.55);
    }
    &:hover {
      color: var(--point-color);
      background-color: #020715;
      background-color: var(--point-bg);
      box-shadow: inset 5px 5px 7px rgba(112, 219, 255, 0.55),
        inset -5px -5px 7px rgba(56, 125, 148, 0.55);
    }
    @media screen and (max-width: 767px) {
      font-size: 1rem;
      padding: 1rem;
      margin: auto;
    }
  `;
  return (
    <Port>
      <div className="porttitle clay">Portfolios</div>
      <div className="PortBtnsWrap">
        {PortList.map((things, idx) => (
          <PortBtn
            className={`${changePort[idx] ? "active" : ""}`}
            key={things + idx}
            onClick={() => {
              changePorts(idx);
            }}
          >
            {things.name}
          </PortBtn>
        ))}
      </div>
      <div className="inner">
        <Monitor folioState={folioState} />
      </div>
    </Port>
  );
};

export default Portfolios;
