import "./App.css";
import { useRef } from "react";

/* Components */
import Intro from "./components/MainSections/Intro";
import About from "./components/MainSections/About";
import Skills from "./components/MainSections/Skills";
import Portfolios from "./components/MainSections/Portfolios";
import Epilogue from "./components/MainSections/Epilogue";

/* Static Components */
import Header from "./components/Static/Header";
import Footer from "./components/Static/Footer";

/* Library */
import GlobalStyle from "./style/GlobalStyle";

function App() {
  const scrollToElement = (locate) => {
    locate.current.scrollIntoView({ behavior: "smooth" });
  };
  /* useRef for Scroll Event */
  const intro = useRef();
  const about = useRef();
  const skills = useRef();
  const folio = useRef();
  const epil = useRef();
  const scrollRef = [intro, about, skills, folio, epil];
  return (
    <>
      <GlobalStyle />
      {/* Header */}
      <Header scrollToElement={scrollToElement} scrollRef={scrollRef} />
      <div className="wrap">
        <Intro />
        <div ref={intro}></div>
        <About />
        <div ref={about}></div>
        <Skills />
        <div ref={skills}></div>
        <Portfolios />
        <div ref={folio}></div>
        <Epilogue />
        <div ref={epil}></div>
      </div>
      <Footer />
    </>
  );
}

export default App;
