import React from "react";
import blogBtn from "../../img/blog/BlogBtn.svg";
import blogBtnHover from "../../img/blog/BlogBtnHover.svg";

import { styled } from "styled-components";
const blogURL = "https://blog.naver.com/rumastudy";

const Epilogue = () => {
  const TitleClay = styled.div`
    width: 700px;
    padding: 1.5rem;
    border-radius: 4rem;
    margin: 3rem auto 1rem;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    & + .epilFlex {
      display: flex;
      width: 100%;
      margin-top: 1rem;
      & > .EpilogueTexts {
        width: 600px;
        position: relative;
        & > p {
          position: absolute;
          left: 0;
          z-index: 30;
          left: 0;
          width: fit-content;
          text-align: left;
          white-space: nowrap;
          font-weight: 700;
          font-size: 32px;
          margin-right: auto;
          margin-bottom: 2.5rem;
          -webkit-text-stroke: 1px var(--point-shadow);
          &:nth-child(1) {
            top: 15%;
          }
          &:nth-child(2) {
            top: calc(15% + 60px);
          }
          &:nth-child(3) {
            top: calc(15% + 120px);
          }
          &:nth-child(4) {
            top: calc(15% + 180px);
          }
          & > b {
            animation-name: pointPara;
            animation-iteration-count: infinite;
            animation-duration: 2500ms;
            -webkit-text-stroke: 1px transparent;
            transition: ease-in-out;
          }
          @keyframes pointPara {
            0% {
              color: var(--point-color);
            }
            50% {
              color: var(--point-shadow);
            }
            100% {
              color: var(--point-color);
            }
          }
        }
      }
      & > .EpilogueBtn {
        flex: 0.8;
        width: 270px;
        height: 270px;
        margin: 3rem auto;
        background-image: url(${blogBtn});
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        cursor: pointer;
        transition: ease 550ms;
        &:hover {
          background-image: url(${blogBtnHover});
        }
      }
    }
    @media screen and (max-width: 767px) {
      width: calc(100% - 1rem);
      padding: 1rem;
      border-radius: 4rem;
      & + .epilFlex {
        flex-direction: column-reverse;
        width: 100%;
        margin-top: 1rem;
        & > .EpilogueTexts {
          width: 100%;
          padding: 1rem;
          & > p {
            width: 100%;
            position: static;
            text-align: center;
            font-weight: 600;
            font-size: 1rem;
            margin-bottom: 0.7rem;
            -webkit-text-stroke: 1px transparent;
            text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
          }
        }
        & > .EpilogueBtn {
          flex: unset;
          width: 100%;
          height: 150px;
          margin: 3rem auto;
        }
      }
    }
  `;
  return (
    <section>
      <div className="inner">
        <TitleClay className="clay">Epilogue</TitleClay>
        <section className="epilFlex">
          <article className="EpilogueTexts">
            <p>비록 제가 지금 당장은 부족할지도 모릅니다.</p>
            <p>하지만, 최선을 다해 노력하여</p>
            <p>
              <b>빠르게 습득하고, 처리할 수 있는</b>
            </p>
            <p>그러한 인재가 되도록 하겠습니다.</p>
          </article>
          <div
            className="EpilogueBtn"
            onClick={() => {
              window.open(blogURL);
            }}
          ></div>
        </section>
      </div>
    </section>
  );
};

export default Epilogue;
